
import moment from 'moment';

export default {
    name: 'future-details',
    props: {
        cityData: {
            type: Object,
            required: true
        }
    },
    computed: {
        tempType(): string {
            return this.$store.getters.tempType;
        }
    },
    methods: {
        skipDay(num: number) {
            return moment().add(num, "day").format("ddd");
        },
        convertToC(num: number) {
            let c = (num - 32) * 5 / 9;
            return Math.floor(c);
        },
        setBgcColor() {
            let num = this.$store.getters.colorNum;
            return (num <= 5 || num >= 20) ? "darkTheme" : "lightTheme";
        }
    },
}
